export function CUPLogoExternalImage({
  ...props
}: {} = {}): ComponentNode<SVGCardLogoProps> {
  const cdnUrl = getLogoCDNUrl(CARD.CUP);

  return <SVGCardLogo {...props} name={CARD.CUP} cdnUrl={cdnUrl} />;
}

export function CUPLogoInlineSVG({
  ...props
}: {} = {}): ComponentNode<SVGCardLogoProps> {
  const svg = getCUPSVG();

  return (
    <SVGCardLogo
      {...props}
      name={CARD.CUP}
      render={() => {
        return svg;
      }}
    />
  );
}

export const CUPLogo = CUPLogoInlineSVG;
